<template>
    <div class="about">
      <div class="minWidth">
        <all_titel :indexText="indexText"></all_titel>
        <div class="productsTitle">
          <div class="productsText">
            关于我们：
          </div>
          <div class="navList">
            <ul>
              <li @click="checkNav(1)" :class="[addClassToBg===1 ? activeClass : '']">企业简介</li>
              <li @click="checkNav(2)" :class="[addClassToBg===2 ? activeClass : '']">愿景与使命</li>
            </ul>
          </div>
        </div>
      </div>
      <div class="pc-page">
        <div class="page1" v-if="pageShow">
          <div class="title">
            品质源于细节，细节铸就品质
          </div>
          <div class="center">
            <div class="text">
              <p>我们是一家集设计生产、销售为一体的专业性厂家，坚持实施“技术创新、管理创新、服务创新”的经营理念，以“互惠互利，共谋发展”为原则。秉承现代欧美简单休闲的艺术风格，以简洁的线条，明快的色调来装饰我们的生活空间。巧妙的将现代美学、心理学、结构学综合体现在产品的设计构造中，使我们产品美观、舒适、实用，为您营造一个清爽、温馨的家。我们以诚为本，以质为胜，用优质产品创建优质品牌，用最超值的价格报答社会</p>
              <p>产品造型美观、款式新颖、线条流畅、储物功能、浪漫色调于一体，荟萃家居装饰工艺精华，是宾馆和家居卫浴的装饰精品。销往全国各地，以它新颖的款式、独特的造型、缤纷的色彩、完善的市场营销体系、周到的服务得到广大用户的一致好评。</p>
              <p>在这里，每个人的理想抱负得以施展，价值得以体现。良好的企业氛围造就了一批行业精英。并在公司内部自觉的形成一种“学习型”文化和团结互助，取长补短共奔目标的亚文化，为公司的技术创新、产品开发、品质的稳定提高奠定了坚实的基础</p>
            </div>
            <div class="img_pro">
              <div class="item-list">
                <div class="item-img">
                  <img src="../../../static/newImg/39.jpg" alt="">
                </div>
                <div class="item-title">高温匠心铸就瓷面</div>
                <div class="item-text">
                  <p>陶瓷采用高温煅烧，减少爆裂现象。致密坚硬手感顺滑，易洁不易挂污，轻松打理</p>
                </div>
              </div>
              <div class="item-list">
                <div class="item-img">
                  <img src="../../../static/newImg/39.jpg" alt="">
                </div>
                <div class="item-title">健康环保，全家放心</div>
                <div class="item-text">
                  <p>严格选材，精湛工艺，精于研发，长于设计，只为生活变得更美</p>
                </div>
              </div>
              <div class="item-list">
                <div class="item-img">
                  <img src="../../../static/newImg/39.jpg" alt="">
                </div>
                <div class="item-title">优质静音，缓冲铰链</div>
                <div class="item-text">
                  <p>高端缓冲自吸导轨，不锈钢铰链。为钢材的使用寿命提供了平稳的舒适环境和运行状态的流畅顺滑</p>
                </div>
              </div>
              <div class="item-list">
                <div class="item-img">
                  <img src="../../../static/newImg/39.jpg" alt="">
                </div>
                <div class="item-title">生态实木，免漆工艺</div>
                <div class="item-text">
                  <p>采用原生木料，精细切割后，90度交错复合而成，表明覆环保装饰面，免漆零甲醛，环保健康</p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="page2" v-if="!pageShow">
          <div class="pageTop">
            <div class="pageLeft">
              <div class="mission">
                <div class="visionTitle">愿景: 创建一流品牌，成为行业标杆</div>
                <div class="missionText">
                  <p>我们要成为整体卫浴行业综合实力前三的著名品牌，并在产品、服务和创新上引领民族卫浴产业的发展</p>
                </div>
              </div>
              <div class="mission">
                <div class="visionTitle">使命: 服务一方，造福一方</div>
                <div class="missionText">
                  <p>我们通过提供品质好、造型美观、款式新颖的产品，使其喜爱并乐于推荐，从而为用户打造舒适、温馨的家。 </p>
                </div>
              </div>
              <div class="mission">
                <div class="visionTitle">价值观: 诚信经营、品质服务、务实共赢</div>
                <div class="missionText">
                  <p>对社会：我们恪守“诚实、守信”的底线，合法合理经营，用优质产品创建优质品牌，用最超值的价格报答社会。</p>
                  <p>对用户：为使用户生活更美好，我们以技术和人才为保障，产品要始终保持行业领先的品质水准，同时提供使其感动的服务。</p>
                  <p>对伙伴（员工、合作伙伴）：我们围绕一个共同目标，以高度主人翁姿态，发扬工匠精神，既踏实苦干，又勇于创新。目的是使员工、所有合作伙伴与企业共创共享，合作互赢。</p>
                </div>
              </div>
            </div>
            <div class="pageRight">
              <img src="../../../static/newImg/bg.png" alt="">
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    import all_titel from '@/components/pageNavigation/pageNavigation'
    export default {
      name: "aboutIndex",
      components:{
        all_titel,
      },
      data(){
          return{
            addClassToBg:1,
            activeClass:'baColor',
            pageShow:false
          }
      },
      computed:{
        about_nav(){
          return this.$store.state.about_nav;
        } ,
        indexText(){
          return this.$store.state.indexText;
        }
      },
      created(){
        var aboutNav = this.$store.state.about_nav;
        if(aboutNav===2){
          this.pageShow=true;
          this.addClassToBg =1;
        }else if(aboutNav===21){
          this.pageShow=false;
          this.addClassToBg =2;
        }
      },
      watch:{
        about_nav(){
          var aboutNav = this.$store.state.about_nav;
          if(aboutNav===2){
            this.pageShow=true;
            this.addClassToBg =1;
          }else if(aboutNav===21){
            this.pageShow=false;
            this.addClassToBg =2;
          }
        }
      },
      methods:{
          checkNav(val){
             if(val===1){
               this.pageShow=true;
                this.$store.dispatch('listenTOsecondNav',2);
                this.$store.dispatch('listenTOAllTitle',['首页','关于我们',]);
                this.addClassToBg = val;
             }else if(val===2){
               this.pageShow=false;
               this.$store.dispatch('listenTOAllTitle',['首页','关于我们','愿景与使命']);
               this.$store.dispatch('listenTOsecondNav',21);
               this.addClassToBg = val;
             }
            $('html,body').animate({
              scrollTop:0
            },500)
          }
      },
    }
</script>

<style scoped>
  .about{
    width: 100%;
    margin:10px auto 0 auto;
    overflow: hidden;
  }
  .minWidth {
    width: 960px;
    margin: 0 auto;
  }
  .productsTitle{
    width: 100%;
    height: 40px;
    overflow: hidden;
    margin-top: 40px;
  }
  .productsText{
    width: 10%;
    font-weight: bold;
    background-color: #e5e5e5;
    text-align: center;
    float: left;
    line-height: 40px;
  }
  .navList{
    width: 90%;
    line-height: 40px;
    background-color: #fafafa;
    float: left;
    overflow: hidden;
  }
  .navList ul {
    width: 45%;
    overflow: hidden;
    display: flex;
  }
  .navList ul li{
    flex: 1;
    text-align: center;
    cursor: pointer;
    margin-right: 10px;
    font-size: 16px;
    color: #999;
  }
  .navList ul li:hover{
    color: #4fb1f7 !important;
  }
  .baColor{
     color: #4fb1f7 !important;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }

  /*企业简介*/
  .page1{
    width: 960px;
    margin: 45px auto  50px auto;
    overflow: hidden;
  }
  .title{
    font-size: 16px;
    font-weight: 400;
    color: #666;
    margin-bottom: 10px;
  }
  .text{
    float: left;
    width: 50%;
    font-size: 14px;
    color: #666;
    margin-right: 3%;
  }
  .text p{
    line-height: 24px;
    text-indent: 2em;
    margin-bottom: 5px;
    font-size: 15px;
    color: #666
  }
  .img_pro{
    float: left;
    width: 47%;
  }
  .item-list{
    width: 48%;
    float: left;
    margin-bottom: 30px;
    overflow: hidden;
    margin-right: 4%;
  }
  .item-list:nth-child(2n){
    margin-right: 0;
  }
  .item-img{
    width: 100%;
    overflow: hidden;
  }
  .item-img img{
    width: 90px;
    height: 90px;
    border-radius: 50%;
    display: block;
    margin: 0 auto;
  }
  .item-title{
    text-align: center;
    font-weight: bold;
    font-size: 14px;
    margin-top: 8px;
    margin-bottom: 5px;
    color: #2a313d;
  }
  .item-text  p{
    font-size: 12px;
    color: #666666;
    text-align: center;
  }

  /*愿景与使命*/
  .page2{
    width: 960px;
    margin: 45px auto  50px auto;
    color: #666;
    position: relative;
  }
  .pageTop{
    width: 100%;
    display: flex;
  }
  .pageLeft{
    flex: 1;
    margin-right: 30px;
  }
  .visionTitle{
    color: rgb(0, 112, 192);
    font-weight: 600;
    margin-bottom: 8px;
  }
  .mission{
    margin-top: 15px;
  }
  .missionText p{
    line-height: 20px;
    margin-bottom: 6px;
    text-indent: 2em;
    font-size: 14px;
    color: #666;
  }
  .pageRight{
    flex: 0 0 450px;
    margin-top: 6%;
  }
  .pageRight img{
    width: 100%;
    display: inline-block;
  }


  /*移动端*/
  @media screen and (max-width: 768px) {
    .minWidth {
      width: 94%;
      margin:  0 auto;
    }
    .page1{
      width: 94%;
      margin:  25px auto 10px auto;
    }
    .page2{
      width: 94%;
      margin:  25px auto;
    }
    .productsTitle{
      width: 100%;
      height: 40px;
      overflow: hidden;
      margin-top: 20px;
    }
    .productsText{
      width: 20%;
      font-weight: bold;
      font-size: 12px;
      background-color: #e5e5e5;
      text-align: center;
      float: left;
      line-height: 40px;
    }
    .navList{
      width: 80%;
      line-height: 40px;
      background-color: #fafafa;
      float: left;
      overflow: hidden;
    }
    .navList ul {
      width:100%;
      overflow: hidden;
      display: flex;
    }
    .navList ul li{
      font-size: 12px;
    }
    /*使命*/
    .pageTop{
      width: 100%;
      display: block;
    }
    .pageLeft{
      margin-right: 0;
    }
    .mission{
      margin-top: 10px;
    }
    .visionTitle{
      font-size: 14px;
      color: rgb(0, 112, 192);
      font-weight: 600;
      margin-bottom: 5px;
    }
    .missionText p{
      line-height: 20px;
      margin-bottom: 6px;
      text-indent: 2em;
      font-size: 12px;
      color: #666;
    }
    .pageRight{
      margin-top: 10px;
    }
    .pageRight img{
      width: 100%;
      display: inline-block;
    }
    /*简介*/
    .title{
      font-size: 14px;
      font-weight: 400;
      color: #666;
      text-align: center;
      margin-bottom: 15px;
    }
    .text{
      width: 100%;
      color: #666;
      margin-right: 3%;
    }
    .text p{
      line-height: 20px;
      text-indent: 2em;
      margin-bottom: 5px;
      font-size: 12px;
      color: #666
    }
    .img_pro{
      width: 100%;
      margin: 10px auto 10px auto;
    }
  }

</style>



